/* LiveChat */

.live-chat {
    position: fixed;
    height: 100vh;
    width: 11vw;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;
    z-index: 5;
    border-left: 2px solid black;
}

.live-chat__title {
    position: absolute;
    width: 100%;
    top: 0;
    padding: .4rem 0;
    visibility: hidden;
    text-align: center;
    font-weight: 900;
    font-size: 1rem;
    background-color: black;
    color: white;
}

/* ChatMessage */

.chat-message {
    padding: 0 .8rem;
    text-align: right;
    font-size: .8rem;
}

.chat-message__author {
    margin: .8rem 0 0 0;
    font-weight: 700;
