}

.chat-message__tag {
    display: inline-block;
    padding: 0 .2rem;
    background-color: black;
    color: white;
    box-shadow: 1px 1px black;
    cursor: pointer;
}

.chat-message__tag:hover {
    background-color: #c00603;
    -webkit-transform: translateX(-.2rem);
            transform: translateX(-.2rem);
}

