    line-height: 5rem;
    background-color: white;
}

/* ForumSearch */

.forum-search {
    grid-area: search-bar;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.forum-search__input {
    width: calc(50% + 1.2vh);
    margin: 0;
    margin-bottom: calc((1rem - 3px) /2);
    padding: .2em 1em;
    border: none;
    border-bottom: 4px solid black;
    background: none;
    font-size: 1.3rem;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    cursor: pointer;
}

.forum-search__icon {
    position: absolute;
    bottom: 1.1rem;
    z-index: -1;
    color: #c8c8c8;
    cursor: pointer;
}

.forum-search__input:hover {
    border-color: #c00603;
}

.forum-search__input::-moz-selection {
    background: #c00603;
    color: white;
}

.forum-search__input::selection {
    background: #c00603;
    color: white;
}

.forum-search__box {
    width: 1rem;
    height: 1rem;
    background: black;
}

.forum-search__box:nth-of-type(1) {
    order: -1;
}

.forum-search__input:hover~.forum-search__box {
    background-color: #c00603;
}

/* ForumSearchPosts */

.forum-search-posts {
    grid-area: 3/1/span 2 / span 4;
    display: grid;
    grid-template-rows: repeat(2, 27vh);
    grid-template-columns: repeat(4, 1fr);
