    font-size: .7rem;
}

/* ForumCategory */

.forum-category-comparison {
    grid-area: 3/1/-1 / span 1;
}

.forum-category-recommendation {
    grid-area: 3/4/-1 / span 1;
}

.forum-category-comparison, .forum-category-recommendation {
    display: grid;
